// @flow
import styled from "react-emotion";

const ModalContent = styled("div")`
  display: flex;
  justify-content: space-between;
  border: 6px solid black;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 1rem;
`;

export default ModalContent;



